import React,{Component} from 'react'
import { hashHistory, Link } from 'react-router'
import { Row, Col, Breadcrumb } from 'antd';

export class RegSuccess extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <Row className="successFrame">
                <Breadcrumb>
                    <Breadcrumb.Item>首页</Breadcrumb.Item>
                    <Breadcrumb.Item>注册成功</Breadcrumb.Item>
                </Breadcrumb>
                <Row className="contentFrame" type="flex" justify="center" align="middle">
                    <Col span={8}>
                        <Col span={12} className="successIcon"></Col>
                        <Col span={12}>
                            <Row className="title">注册成功!</Row>
                            <Row>您可以:</Row>
                            <Row>1.回到<Link to="/">首页</Link></Row>
                            <Row>2.去<Link to={`/${this.props.location.query.type}/space`}>用户中心</Link>完善您的个人资料</Row>

                        </Col>
                    </Col>
                </Row>
            </Row>
        )
    }
}

module.exports = RegSuccess;


